@import "styles/global/colors.scss";

.app-layout-sidebar {
	background-color: $color-sidebar;
	width: 20%;

	display: flex;
	flex-direction: column;
	justify-content: space-between;

	.sidebar-logo {
		padding: 10px 20px 0 20px;
		img {
			width: 100%;
			height: auto;
		}
	}

	.sidebar-links,
	.sidebar-bottom-links {
		padding: 0;
		margin: 0;
		li {
			list-style: none;
			width: 100%;
			text-align: left;
			display: flex;
			text-decoration: none;
			a {
				width: 100%;
				height: 100%;
				padding: 16px 8px;
				border-left: 2px solid transparent;
				color: lightgray;
				&:hover {
					border-left-color: aqua;
					color: white;
					
				}
			}
		}
	}

	.sidebar-bottom-links {
		align-self: flex-end;
	}
}