
.app-layout-header {
	box-shadow: 0px 2px 2px rgba(0,0,0,0.5);
	z-index: 1;
	display: flex;
	justify-content: space-between;

	max-height: 80px;

	.nav-links {
		display: flex;
		justify-content: flex-end;

		li {
			list-style: none;
			a {
				text-decoration: none;
				font-size: 2em;
			}
		}
	}

	.menu-container,
	.bell-container,
	.account-name-container,
	.account-photo-container,
	.account-details-container {
		display: flex;
		justify-content: center;
		flex-direction: column;
		padding-left: 4px;
		padding-right: 4px;
		position: relative;
	}
	.account-photo-container {
		img {
			max-height: 100%;
		}
	}

	.header-right {
		display: flex;
		flex-direction: row;
		padding-right: 16px;
	}
	.bell-container {
		margin-right: 20px;
	}

	.details-account-menu {
		background-color: white;
		box-shadow: 0px 2px 2px rgba(0,0,0,0.5);
		display: flex;
		flex-direction: column;
		border-radius: 5px;

		a {
			padding: 10px;
			white-space: nowrap;
		}
	}
}
